<template>
  <div class="about">
    <h1>This is an about page</h1>
    <b-button label="Logout" @click.prevent="submit()"></b-button>
    <b-button
      type="is-primary"
      tag="router-link"
      to="paste/4"
      label="Ver Paste">
    </b-button>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'

  export default{
    methods: {
      ...mapActions({
        logout: 'auth/logout'
      }),

      async submit(){
        await this.logout()
      }
    },

  }
</script>
